*{margin: 0; padding: 0;
    font-family: 'Noto Sans KR', sans-serif;}
 img{border:0}
 ul,li{ list-style: none;}
 a{text-decoration: none;}

.detail_area{ width: 100%; height: 2100px; background-color: #f5f5dc;}




.detail_area_title{ width: 100%;height: 80px; background-color: rgb(226, 226, 200);
                  line-height: 80px;text-align: center; color: #333;
                  font-size: 20px;}

.detail_area_in{margin: 0px auto; 
              width: 1500px; height: 2100px;
              background-color: #f5f5dc;}

.detail_area_in .detail_area_inner{margin: 30px auto; width: 1500px;height: 600px;}    


.detail_area_in .detail_logo{float: left; width: 500px; height: 600px; background-color: #f5f5dc; }
.detail_area_in .detail_logo .logo_img{width:150px; height: 50px;padding-top: 30px;}
.detail_area_in .detail_img{float: left; width: 500px; height: 600px; background-color: #f5f5dc; }


.detail_area_in .detail_text{float: left;width: 500px;height: 600px;background-color: #f5f5dc;}

.detail_text .detail_textbox{margin-top: 80px; width: 100%; height: 150px;
                             border-bottom: 1px solid #333; }
.detail_text .detail_textbox  .subtitle{font-size: 13px; }
.detail_text .detail_textbox  .title{margin-top: 20px; font-size: 24px;}
.detail_text .detail_textbox  .content{margin-top: 20px;width: 500px;height:50px; font-size: 15px;}

.con{ margin-top: 30px;font-size: 15px; }

.open{cursor: pointer;}

.baro{ width: 300px;height:50px;
    text-align: center; line-height: 50px;
    border:  1px solid #333;
    background-color: #f5f5dc; color: #333;
    cursor: pointer;
    margin-top: 20px;
    margin-left:100px ;}
.baro:hover{background-color: #333;color: #fff;}






.service{margin-top: 50px; width: 1500px; height: 150px; background-color: #f5f5dc;}
.service .service_left{float: left;margin-left: 70px; margin-top: 10px;
                       width: 600px;height: 130px; }
.service .service_left p{text-align: center; padding-top: 40px;font-size: 13px;}   
.service .line{float: left;margin-left: 80px;margin-top: 40px;  width: 1px;height: 70px; background-color: #999;}
.service .service_right{float: right;margin-right: 70px; margin-top: 10px;
                        width: 600px;height: 130px; }
.service .service_right p{text-align: center; padding-top: 40px;font-size: 13px;}  





.banner{ margin: 50px auto; width: 1500px; height: 500px; }

.banner .banner_img{float: left; width: 750px;height: 500px; background-color: #ededde;}
.banner .banner_img .imginner{width: 650px; height: 400px;margin-left: 50px;margin-top: 50px;}


.banner .banner_inner{float: left; width: 750px;height: 500px;background-color: #f5f6e8; }

.banner .banner_text{margin-top: 100px; margin-left: 100px; width: 450px; height: 450px;}

.banner .banner_text span{margin-top: 10px;  font-size: 13px; }
.banner .banner_text .title{margin-top: 30px; font-size: 24px;border-bottom: 1px solid #333;  }
.banner  .banner_text  ul{margin-top: 30px; width: 100%;height: 150px; 
    -webkit-padding-start: 0px; }
.banner  .banner_text  ul>li{width: 100%;height: 50px;
                           line-height: 50px;font-size: 15px;}






.listitems{margin-top: 100px; width: 1500px;height: 450px;background-color: #f5f5dc;}
.listitems .use{float: left;width: 350px;height: 450px;background-color: #f5f5dc;}
.listitems .use p{font-size: 24px; padding-top: 30px; }
/* card 레이아웃 */
.list{float: left;margin: 10px 0 20px 20px; width: 350px;height: 450px;
    cursor: pointer; }
.list:hover{background-color:rgb(226, 226, 200);}
/* card 이미지 */
.list_img{width: 100%; height: 350px;}

/* card- data */
.list .list_text{width: 100%;height: 100px;}

.list .list_text h3{text-align: center; font-size: 13px; color: #333;
                               padding-top: 25px;}
.list .list_text p{font-size: 12px;color: #333;text-align: center; padding-top: 25px;}






.modal_bg{position: fixed;z-index: 9999;
          left: 0;top: 0;
          width: 100%;height: 100%;
          background-color: rgba(0, 0, 0, 0.2);}

.modal_bg .modal_inner{position: absolute;left: 50%;top: 50%;
                 transform: translate(-50%,-50%);
                 width: 700px;height: 500px;
                 background-color: #f1f0e8;
                 padding: 30px;}

.modal_bg .modal_inner .title{width: 610px;height: 200px;
                              margin-left: 15px;}
.modal_bg .modal_inner .title h2{float: left; height: 50px;line-height: 50px;
                                 font-size: 1.2rem;padding-left: 30px;
                                 padding-top: 30px;
                                 }
.modal_bg .modal_inner .title .close{float: left; margin-left: 520px; margin-top: 40px; cursor: pointer;}

.modal_bg .modal_inner .title ul{width: 610px;height: 400px; padding-top: 80px;}
.modal_bg .modal_inner .title ul>li{padding-top: 50px; font-size: 1rem;}