#wrap {margin: 0; padding: 0 ;}
#brandVisual { position:relative;width:100%; min-width:1500px; margin:0 auto; overflow:hidden;height:550px; background:#8c908c;}
#brandVisual ul { position:relative; float:left; width:100%; height:550px;}
#brandVisual li { position:absolute; width:100%;height:550px;}
#brandVisual li a {display:block;text-indent:-9999px} 

#brandVisual .visual_0 {left:0px; background:url("/public/img/slide01.jpg") 50% 0 no-repeat;}
#brandVisual .visual_1 {left: 100%;  background:url("/public/img/slide0202.jpg") 50% 0 no-repeat;}
#brandVisual .visual_2 {left: 200%; background:url("/public/img/slide0404.jpg") 50% 0 no-repeat;}

#buttonList:after {content:""; display:block; clear:both;}
 #buttonList li{ background:url("/public/img/btnVisual.png") 0 -16px no-repeat; width:14px; height:15px; float:left; position:relative; left:50%; top:-135px; overflow:hidden; margin:0 10px; cursor:pointer;}
#buttonList li.on { background-position:0 0;} 
 #buttonList li a {display:block;text-indent:-9999px}





















































