.cart_area{ width: 100%; height: 660px; background-color: #333;}

.cart_area_title{ width: 100%;height: 80px; background-color: #333;
                  line-height: 80px;text-align: center; color: #fff;
                  font-size: 20px;}

.choice{width: 100%;height: 40px; background-color: #333;
      }

.choice .choice_inner{margin-left: 350px; width: 1200px; height: 40px; background-color: #333;
                      border-bottom: 1px solid #fff;}      
.choice .alldelete{float: left; width: 100px; height: 40px; line-height: 40px;
                   font-size: 15px;
                   color: #fff;cursor: pointer;}
.choice .delete{float: left;width: 100px;height: 40px;
                line-height: 40px; text-align: center;
                color: #fff; font-size: 15px;
                margin-left: 860px;cursor: pointer;}
.choice .line{float: left;width: 1px;height: 20px;background-color: #999;
              line-height: 30px;
              margin-left: 5px; margin-top: 10px;}
.choice .soldedelete{float: left;width: 100px;height: 40px;
                   line-height: 40px; text-align: center;
                   color: #999; font-size: 15px;
                   margin-left: 20px;cursor: pointer;}






.cart_area_in{float: left; margin-top: 40px;
              margin-left: 350px;
              width: 900px; height: 490px;
              background-color:#333}       
             
              
              table {
                width: 900px;
             

                border-collapse: collapse;
                background-color: #333;
                color: #fff;
              }

              thead {
                font-size: 16px;
              }
              
              tbody {
                font-size: 13px;
              }

              th, td {
                /* border: 1px solid #fff; */
                border-bottom: 1px solid #999;
                padding: 20px;

              }

              .btn_up{width: 25px; height: 25px; margin-left: 10px; background-color: #f5f5dc;}
              .btn_down{width: 25px; height: 25px; margin-left: 5px; background-color: #f5f5dc;}


              .cart_area_inner{float: left; width: 300px;height: 520px; background-color: #333;}   
              
              .cart_area_inner .logo{margin-left: 150px; margin-top: 30px;}




              .cart_area_inner .pay_title{ width: 300px;height: 20px;
                                          margin-top: 240px;}
              .cart_area_inner .pay_title p{font-size: 13px;color: #fff;}


               .cart_area_inner .pay_price{ width: 300px;height: 20px;
                                            margin-top: 30px;}
                .cart_area_inner .pay_price p{float: left; font-size: 13px;color: #fff;}
                .cart_area_inner .pay_price span{float: left; font-size: 16px;color: #fff; padding-left: 120px;}



              .cart_area_inner .pay{width: 300px;height: 50px;margin-top: 30px; border: 1px solid #fff; }
              .cart_area_inner .pay p{width: 300px;height: 50px;line-height: 50px;
                                     font-size: 16px;color: #fff;text-align: center;
                                     cursor: pointer;}
              .pay p:hover{ background-color: #f5f5dc; color: #333;}    

              