*{margin: 0; padding: 0;
       font-family: 'Noto Sans KR', sans-serif;}
    img{border:0}
    ul,li{ list-style: none;}
    a{text-decoration: none;}




    
.contents{ float: left; width: 100%; 
          background-color: #f5f5dc;}
/*=====Main-img===== */
.main_img{width: 100%;height: 550px;background-color: #333;}










.contents_in{margin: 0 auto; width: 1500px;background-color:beige;}







/* =====fst-con===== */
.fst{   margin: 200px auto; 
        width: 1500px; height: 450px;
        /* background-color:beige; */}

/* ===건성피부이미지 */
.fst_in{float: left; width: 350px;height: 450px;}
 
.fst_text{width: 350px;height: 100px;background-color: #f5f5dc;}
.fst_text span{font-size: 24px; }
.fst_text div{padding-top: 20px; font-size: 13px; }



.fst_iner {float: left;margin-left: 20px; width: 1130px;height: 450px;}   

/* card 레이아웃(fst/lst) */
.item{float: left;margin: 0 0 20px 25px; width: 350px;height: 450px;cursor: pointer;  }
.item:hover{background-color:  rgb(226, 226, 200);}
/* card 이미지 */
.item_img{width: 100%; height: 350px; }

/* card- data */
.item .item_text{width: 100%;height: 100px;}

.item .item_text h3{text-align: center; font-size: 13px; color: #333;
                                 padding-top: 30px;}
.item .item_text p{font-size: 12px;color: #333;text-align: center; padding-top: 10px;}






/* =====banner===== */
.banner{margin: 200px 0; 
       width: 100%;height: 450px;}
.banner .banner_inner{float: left; width: 550px;height: 500px;}

.banner .banner_text{margin-top: 50px; width: 100%; height: 400px; }

.banner .banner_text span{font-size: 13px;}
.banner .banner_text .title{margin-top: 30px; font-size: 24px;}
.banner .banner_text p{margin-top: 30px; font-size: 15px;}

.banner .banner_baro{width: 180px;height:50px;
                     text-align: center; line-height: 50px;
                     border:  1px solid #333;
                     background-color: #f5f5dc; color: #333;
                     cursor: pointer;
                     margin-top: 50px;}
.banner .banner_baro:hover{background-color: #333;color: #fff;}


.banner .banner_img{float: left; width: 950px;height: 500px;}






/* =====mid-con===== */
.mid{margin: 200px 0; 
      width: 1500px; height: 450px;}

.mid_in{float: left; width: 350px;height: 450px; }

.mid_text{width: 350px;height: 100px;background-color: #f5f5dc; }
.mid_text span{ font-size: 24px; }
.mid_text div{ padding-top: 20px; font-size: 13px;}

.mid_iner {float: right; margin-right: 20px;width: 1130px;height: 450px;  }  









/* =====store===== */
.store{margin: 200px 0; 
       width: 1500px; height: 450px;}
       .store .store_inner{float: left; width: 550px;height: 500px;background-color: #f5f5dc;}
       .store .store_text{margin-top: 50px; width: 100%; height: 400px;  }

       .store  .store_text span{font-size: 13px;}
       .store  .store_text .title{margin-top: 30px; font-size: 24px;}
       .store  .store_text  ul{ margin-top: 30px; width: 100%;height: 150px; 
                                -webkit-padding-start: 0px;}
       .store  .store_text  ul>li{width: 100%;height: 50px;
                                  line-height: 50px;font-size: 15px;}



       .store .store_baro{width: 180px;height:50px;
                          text-align: center; line-height: 50px;
                          border:  1px solid #333;
                          background-color: #f5f5dc; color: #333;
                          cursor: pointer;
                          margin-top: 30px;}
       .store .store_baro:hover{background-color: #333;color: #fff;}
       
       
       .store .store_img{float: left; width: 950px;height: 500px;}






/* =====lst-con===== */
.lst{ margin-top: 200px; 
           width: 1500px; height: 450px; 
           /* background-color: #333; */}
.lst_in{float: left; width: 350px;height: 450px;}

.lst_text{width: 350px;height: 100px;background-color: #f5f5dc;}
.lst_text span{font-size: 24px;}
.lst_text div{padding-top: 20px; font-size: 13px;}
.lst_iner {float: left;margin-left: 20px; width: 1130px;height: 450px;}   





.brand_title{margin:100px 0 0 0 ; 
                  width: 1500px;height: 100px;
                  background-color:#f5f5dc;
                  text-align: center; line-height: 100px;
                  font-size: 24px;
                  }





.brand{margin:50px 0 50px 0 ; 
            width: 1500px; height: 450px;
            display: flex;
            justify-content: space-between;
            background-color: #f5f5dc;}

.brand .brand_box{ width: 470px; height: 450px;
                   margin:0  0px  0  0 ; 
                   background-color:#f5f6e8; }


.brand_box .brand_img{width: 430px;height: 240px;  
                      margin-top: 20px;
                      margin-left: 20px;}

.brand_box .brand_text{margin-top: 20px; width: 470px;height: 170px;
                       background-color:#f5f6e8;}
.brand_box .brand_text .brand_text_inner{padding: 0px 20px 0px 20px;color: #333;}
.brand_box .brand_text .brand_text_inner h3{font-size: 16px;}
.brand_box .brand_text .brand_text_inner p{font-size: 13px; padding-top: 10px;}







.brand .brand_box02{ width: 470px; height: 450px;
       margin:0  0px  0  0 ; 
       background-color:#ede1d5; }


.brand_box02 .brand_img{width: 430px;height: 240px;  
          margin-top: 20px;
          margin-left: 20px;}

.brand_box02 .brand_text02{margin-top: 20px; width: 470px;height: 170px;
           background-color:#ede1d5;}
.brand_box02 .brand_text02 .brand_text_inner02{padding: 0px 20px 0px 20px;color: #333;}
.brand_box02 .brand_text02 .brand_text_inner02 h3{font-size: 16px;}
.brand_box02 .brand_text02 .brand_text_inner02 p{font-size: 13px; padding-top: 10px;}





