*{margin: 0; padding: 0;
      font-family: 'Noto Sans KR', sans-serif;}
   img{border:0}
   ul,li{ list-style: none;}
   a{text-decoration: none;}

.best_area{ width: 100%; height: 4800px; background-color: #f5f5dc;}




.best_area_title{ width: 100%;height: 80px; background-color: rgb(226, 226, 200);
                  line-height: 80px;text-align: center; color: #333;
                  font-size: 24px;}





.best_area_in{margin: 10px auto; 
              width: 1500px; height: 4000px;
              background-color: rgb(226, 226, 200);}
 /* 클렌저 */
.best_area_in .best_area_cls{margin-bottom: 20px; width: 1500px;height: 300px;background-color: beige;}
.best_area_cls h3{padding: 45px 0 0 10px;font-size: 20px;}
.best_area_cls p{padding: 45px 0 0 10px; cursor: pointer;}



/* card 레이아웃 */
.list{float: left;margin: 10px 0 20px 20px; width: 350px;height: 450px;
      cursor: pointer; }
.list:hover{background-color: #f5f5dc;}
/* card 이미지 */
.list_img{width: 100%; height: 350px;}

/* card- data */
.list .list_text{width: 100%;height: 100px;}

.list .list_text h3{text-align: center; font-size: 13px; color: #333;
                                 padding-top: 25px;}
.list .list_text p{font-size: 12px;color: #333;text-align: center; padding-top: 25px;}


/* 각질제거 / 토너 / 하이드레이터 / 선케어  */
.best_area_in .best_area_skin{margin: 500px 0 20px 0;  width: 1500px; height: 300px; background-color: #f5f5dc;}
.best_area_skin h3{padding: 45px 0 0 10px;font-size: 20px;}
.best_area_skin p{padding: 45px 0 0 10px;cursor: pointer;}





.banner{margin: 100px auto;  width: 1500px; height: 500px; }

.banner .banner_inner{float: left; width: 550px;height: 500px;}

.banner .banner_text{margin-top: 50px; width: 100%; height: 400px; }

.banner .banner_text span{font-size: 13px; }
.banner .banner_text .title{margin-top: 30px; font-size: 24px;}
.banner  .banner_text  ul{margin-top: 30px; width: 100%;height: 150px;
                          -webkit-padding-start: 0px;}
.banner  .banner_text  ul>li{width: 100%;height: 50px;
                           line-height: 50px;font-size: 15px;}

.banner .banner_baro{ width: 300px;height:50px;
                     text-align: center; line-height: 50px;
                     border:  1px solid #333;
                     background-color: #f5f5dc; color: #333;
                     cursor: pointer;
                     margin-top: 130px;}
.banner .banner_baro:hover{background-color: #333;color: #fff;}


.banner .banner_img{float: left; width: 950px;height: 500px;}













