*{margin: 0; padding: 0;
    font-family: 'Noto Sans KR', sans-serif;}
 img{border:0}
 ul,li{ list-style: none;}
 a{text-decoration: none;}

 
 
.footer{   float: left; width: 100%; height: 445px;
           background-color: #f5f5dc; color: #333;position: relative;}

.footer .footer_in{margin: 20px auto; width: 1500px;height: 300px; background-color: #f5f5dc;
                   border-top: 1px solid #000;
                   border-bottom: 0.5px solid #000;}
/*==========footer-text========== */
.footer .footer_in .footer_text{float: left; width: 470px; height: 300px;}

.footer_text .logo{width:150px; height: 50px;padding-top: 50px; padding-left: 80px;} 
.footer_text p{padding-left: 80px; padding-top: 70px; font-size: 13px;}





/*==========footer-menu========== */
.footer_menu{float: left;margin-left: 43px; width: 300px;height: 300px; }

.footer_menu .sub_title{margin-left: 50px; width: 150px;height: 80px; border-bottom: 1px solid #000;}
.footer_menu .sub_title>p{padding-top: 40px; font-size: 14px;}

.footer_menu ul.sub_menu{width: 300px;height: 230px; -webkit-padding-start: 0px; cursor: pointer;}
.footer_menu ul.sub_menu>li{padding-left: 50px; padding-top: 25px; font-size: 13px;}



/*==========footer-copy========== */
.footer .footer_copy{margin: 10px auto; width: 1500px;height: 100px; background-color:#f5f5dc; }


.footer_copy .copy_logo{float: left; width:150px; height: 50px;
                        font-size: 12px;
                        padding-left: 80px;padding-top: 30px;}
.footer_copy .copy_text{float: left; width: 900px; height: 30px;
                        margin-top: 20px; margin-left: 330px;
                        font-size: 12px; }
