*{margin: 0; padding: 0;
    font-family: 'Noto Sans KR', sans-serif;}
 img{border:0}
 ul,li{ list-style: none;}
 a{text-decoration: none;}




/* =====상단팝업창===== */




/* =====header===== */
.header{ width: 100%; height: 110px;
         display: flex; flex-direction: row; 
         justify-content: center;
         background-color: #f5f5dc;
         position: relative;}

.popup{width: 100%;height: 40px;background-color: #333;
              position: absolute;left: 0;top: 0;}
.popup .popup_title{width: 400px;height: 40px;margin:  0 auto;}            
.popup .popup_title p{text-align: center; line-height: 40px; 
                      font-size: 13px; color: #fff;}
.popup .popup_title span{line-height: 40px; 
                        
                         padding-left: 5px;
                         font-size: 13px;
                         color: #fff; 
                         cursor: pointer;}
.popup .popup_title span:hover{border-bottom:1px solid #fff;}




.header .header_in{ width: 1200px; height: 110px;
                    display: flex; flex-direction: row; 
                    justify-content: center;
                    background-color: #f5f5dc; }         
/* =====logo===== */
.header .logo{width:150px; height: 50px;padding-top: 50px;}









/* =====네비게이션====== */
.header .nav{width:900px; height: 100px;  
              }

.header .nav ul{margin: 0 auto; width: 700px;
                  text-align: center;
                  margin-top: 70px;}
.header .nav li{ float: left;
                   width:20%; height: 30px; 
                   line-height: 30px;font-size: 16px;font-weight: 500;}

                   

/* =====로그인/장바구니===== */
.header .info_mn{width: 150px;height: 20px;
                 padding-top: 75px;}
.header .info_mn ul{width: 100%;text-align: center; }
.header .info_mn li{float: left;padding-left: 15px;
                     color: #333;}



/* =====모달창===== */

.modal_bg{position: fixed;z-index: 9999;
        left: 0;top: 0;
        width: 100%;height: 100%;
        background-color: rgba(0, 0, 0, 0.2);
        }
.modal_bg .modal_inner{position: absolute;left: 50%;top: 50%;
                     transform: translate(-50%,-50%);
                     width: 700px;height: 800px;
                     background-color: #f1f0e8;
                     padding: 50px;}
.modal_bg .modal_inner .title{width: 600px;height: 50px; border-bottom: 1px solid #999;}
.modal_bg .modal_inner .title h2{float: left; height: 50px;line-height: 50px;
                                font-size: 1rem;}
.modal_bg .modal_inner .title .close{float:right;  margin-top: 10px; cursor: pointer;}


.modal_bg .modal_inner .content{width: 600px;height: 120px; border-bottom: 1px solid #999;}
.modal_bg .modal_inner .content h3{font-size: 0.9rem;padding-top: 20px;}
.modal_bg .modal_inner .content p{font-size: 0.9rem; float: left; padding-top: 40px; 
                                  padding-right: 110px;}
.modal_bg .modal_inner .content span{ float: right; font-size: 0.9rem;
                                      margin-top: 40px;  }





.modal_bg .modal_inner .contents{width: 600px;height: 130px; border-bottom: 1px solid #999;}
.modal_bg .modal_inner .contents h3{font-size: 0.9rem;padding-top: 20px;}
.modal_bg .modal_inner .contents p{font-size: 0.9rem; float: left; padding-top: 25px; 
                                  }

.modal_bg .modal_inner .contents01{width: 600px;height: 140px; border-bottom: 1px solid #999;}
.modal_bg .modal_inner .contents01 h3{font-size: 0.9rem;padding-top: 20px;}
.modal_bg .modal_inner .contents01 p{font-size: 0.9rem; float: left; padding-top: 15px; 
                                  }
.modal_bg .modal_inner .contents02{width: 600px;height: 130px; }
.modal_bg .modal_inner .contents02 h3{font-size: 0.9rem;padding-top: 20px;}
.modal_bg .modal_inner .contents02 p{font-size: 0.9rem; float: left; padding-top: 20px; 
                                  }

